<template>
    <div class="dialogUpload" v-if="$store.state.dialogUpload">
        <el-dialog 
            :visible.sync="$store.state.dialogUpload" 
            :modal="false" 
            :show-close="false" 
            :close-on-click-modal="false"
             v-loading="loading"
            element-loading-text=""
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.1)">
            <div class="upload-container">
                <p></p>
                <div class="uploader" v-show="uploadshow">
                    <div class="uploader_area">
                        <el-upload
                            :visible.sync="$store.state.dialogUpload"
                            ref="upload"
                            :modal="false"
                            drag
                            :auto-upload="false"
                            :limit="1"
                            action="/api/material/uploadmaterial"
                            :data = "fileInfo"
                            :on-success="uploadSuccess"
                            :on-error="uploadError"
                            >
                            <el-button type="primary"></el-button>
                            <p></p>
                        </el-upload>
                    </div>
                    <div class="upload_limit">
                        <p></p>
                        <p>2D:jpg,png,dds,tga</p>
                        <p>3D:fbx,obj,dae,max,stl,pmx,vmd</p>
                        <p>:MP3</p>
                        <p>,20M</p>
                    </div>
                </div>
                <div v-show="!uploadshow">
                    <el-form :label-position="labelPosition" label-width="80px" :model="fileInfo" class="fileinfo">
                        <el-form-item label="">
                            <el-select v-model="fileInfo.type" placeholder="">
                                <el-option label="2D" value="1"></el-option>
                                <el-option label="3D" value="2"></el-option>
                                <el-option label="" value="3"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="fileInfo.title"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-cascader
                                expand-trigger="hover"
                                :options="options"
                                v-model="selectedOptions"
                                @change="handleChange">
                            </el-cascader>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="upload_footer">
                    <el-button @click="quitUpload"></el-button>
                    <el-button type="primary" @click="submitnext" v-if="uploadshow"></el-button>
                    <el-button type="primary" @click="submitUpload" v-else></el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import store from '@/vuex/store'
import {mapState,mapMutations ,mapGetters ,mapActions} from 'vuex'
export default {
    data () {
        return {
            loading:false,
            uploadshow:true,
            labelPosition:'left',
            selectedOptions: [],
            fileInfo:{
                title:'',
                type:'',
                filetype:'',
                userid:'',
                state:1
            },
            options:[
                {
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                }
            ],
        }
    },
    methods: {
        ...mapMutations(["changedialogUpload"]),
        quitUpload() {
            let self = this;
            this.Confirm('','',{
                confirmmessage:'',
                confirmCb() {
                    self.changedialogUpload();
                    self.uploadshow = true;
                }
            })
        },
        submitnext() {
             this.uploadshow = false;
            //  console.log(this.$refs.upload);
        },
        submitUpload() {
            let userid = this.$store.state.userid;
            if(!userid) {
                this.Message('','warning');
                return fasle;
            }else if(!this.fileInfo.title) {
                this.Message('','warning');
                return fasle;
            }else if(!this.fileInfo.filetype) {
                this.Message('','warning');
                return fasle;
            }
            this.fileInfo.userid = userid;
            this.$refs.upload.submit();
            this.loading = true;
            
            // console.log(this.$refs.upload);
        },
        handleChange(value) {
            // console.log(value);
            this.fileInfo.filetype = `${value[0]}-${value[1]}`;
        },
        uploadSuccess(response, file, fileList) {
            this.loading = false;
            this.Message('','success');
            this.uploadshow = true;
            this.changedialogUpload();
            // console.log(response);
            let id = response.data.id;
            this.$router.push(`/model/draft/${id}`)
        },
        uploadError(response, file, fileList) {
            this.loading = false;
            this.Message('','error');
        },
    }
}
</script>

<style lang="less" scoped>
    .dialogUpload{
        width: 100vh;
        height: 100vh;
    }
</style>






