<template>
    <div class="dialogSignup" v-if="$store.state.dialogSignup">
        <el-dialog :visible.sync="$store.state.dialogSignup" :modal="false" :show-close="false">
            <div class="dilog-left fl">
                <p></p>
                <el-form :model="formSignup" :rules="rules2">
                    <el-form-item prop="username">
                        <el-input v-model="formSignup.username" auto-complete="off" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="mail">
                        <el-input type="eamil" v-model="formSignup.mail" auto-complete="off" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" v-model="formSignup.password" auto-complete="off" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" v-model="formSignup.surepassword" auto-complete="off" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="agreement" class="agreement">
                        <el-checkbox></el-checkbox>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="Signupbtn"></el-button>
                    <div>
                        <span class="fr"><span class="color-font cur" @click="changedialogLogin"></span></span>
                    </div>
                </div>
            </div>
            <div class="dialog-right fl">
                <img src="" alt="">
            </div>
        </el-dialog>
    </div>
</template>
<script>
import NodeRSA from 'node-rsa';
import store from '@/vuex/store'
import {mapState,mapMutations ,mapGetters ,mapActions} from 'vuex'
import func from '@/public/func';
import api from '@/public/api';
export default {
    data () {
        return {
            formSignup: {
                username: '',
                password: '',
                mail:'',
            },
            rules2:{
                username: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
                ],
                mail:[
                    { required: true, message: '', trigger: 'blur' },
                    // { type:mail, message: 'email', trigger: 'blur' }
                ],
                password:[
                    { required: true, message: '', trigger: 'blur' },
                    {min:6,max:12, message: ' 6  12 ', trigger: 'blur' }
                ],
                agreement:[
                    { required: true, message: '', trigger: 'change' }
                ]
            },
        }
    },
    store,
    methods: {
        ...mapMutations(["changedialogLogin","changedialogSignup"]),
        Signupbtn() {
            let publicKey = this.$store.state.publicKey;
            let privatekey = new NodeRSA(publicKey);
            let password = privatekey.encrypt(this.formSignup.password,'base64');
            func.ajaxPost(
                api.signup,
                {
                    username:this.formSignup.username,
                    mail:this.formSignup.mail,
                    password:password,
                },
                res => {
                    if(res.data.status==200) {
                        if(res.data.data) {
                            this.changedialogSignup();
                            this.Message(res.data.data.msg,success)
                        }else if(res.data.errmsg) {
                            this.Message(errmsg,error)
                        }
                    }else if(res.data.status == 500){
                        this.Message(errmsg,error)
                    }
                }
            )
        },
    },
}
</script>
<style lang="less">
    .dialogSignup{
        .el-dialog__body{
            padding: 30px 20px;
        }
        .dilog-left{
            padding: 0 20px;
        }
        .el-dialog__header{
            padding: 0;
        }
        .el-input .el-input__inner{
            width: 260px;
            height: 32px;
        }
    }
</style>





