<template>
    <div id="draft">
        <aside>
            <div class="person-oprate">
                <el-button v-moduleQuit="{userid:this.$store.state.userid}"></el-button>
                <el-button type="primary" @click="saveModule"></el-button>
                <el-button type="success" @click="pubishModule"></el-button>
            </div>
            <div>
                <el-form :label-position="labelPosition" label-width="80px" :model="fileInfo" class="fileinfo">
                    <el-form-item label="">
                        <el-input v-model="fileInfo.title"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-cascader
                            expand-trigger="hover"
                            :options="options"
                            v-model="selectedOptions"
                            @change="handleChange">
                        </el-cascader>
                    </el-form-item>
                    <el-form-item label="">
                        <div class="el-input">
                            <div :class="[{'getFocus':isFouce},'tag_box']">
                                <div class="tag" v-for='(tag,index) in taglist' :key="index">
                                    <span>{{tag}}</span>
                                    <span class="iconfont icon-cuowu" @click="deleteTag(index)"></span>
                                </div>
                                <input type="text" class="tagInput" v-model="tag" placeholder="," @focus="getFocus" @blur="getBlur" @keydown.enter="addTag">
                            </div>
                        </div>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="textarea"
                            :rows="3"
                            placeholder=",!"
                            v-model="fileInfo.desc">
                        </el-input>
                    </el-form-item>
                </el-form>
            </div>
          
      </aside>
    </div>
</template>
