                    },{
                        value:'',
                        label:''
                    },]
                },{
                    value:'',
                    label:'',
                    children:[{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },{
                        value:'',
                        label:''
                    },]
                }
            ],
            taglist:[],
            tag:'',

        }
    },
    store,
    methods: {
        handleChange(value) {
            this.fileInfo.filetype = `${value[0]}-${value[1]}`;
        },
        saveModule() {
            if(this.taglist.length !== 0) {
                this.fileInfo.tags = JSON.stringify(this.taglist);
            }
            let id = this.$route.params.id,
                userid = this.$store.state.userid,
                title = this.fileInfo.title,
                filetype = this.fileInfo.filetype,
                tags = this.fileInfo.tags,
                desc = this.fileInfo.desc;
            func.ajaxPost(
                api.dealmodule,
                {
                    id:id,
                    title:title,
                    filetype:filetype,
                    tags:tags,
                    desc:desc,
                    state:0,
                },
                res => {
                    if(res.data.status === 200) {
                        if(res.data.data) {
                            this.Message('','success')
                        }
                    }
                }
            )
        },
        pubishModule() {
            let id = this.$route.params.id,
                userid = this.$store.state.userid,
                title = this.fileInfo.title,
                filetype = this.fileInfo.filetype,
                tags = this.fileInfo.tags,
                desc = this.fileInfo.desc;
            func.ajaxPost(
                api.dealmodule,
                {
                    id:id,
                    title:title,
                    filetype:filetype,
                    tags:tags,
                    desc:desc,
                    state:1,
                },
                res => {
                    if(res.data.status === 200) {
                        if(res.data.data) {
                            this.Message('','success');
                            this.$router.push(`/mine/${userid}`)
                        }
                    }
                }
            )
        },
        getFocus() {
            this.isFouce = true;
        },
        getBlur () {
            this.isFouce = false;
        },
        addTag() {
            if(['','undefind','null'].includes(this.tag)) {
                this.Message('','warning');
                return false;
            }else if(this.taglist.includes(this.tag)) {
                this.Message('','warning');
                return false;
            }
            this.taglist.push(this.tag);
            this.tag = '';
            console.log(this.taglist);
        },
        deleteTag(index) {
            this.taglist.splice(index,1);
            console.log(this.taglist)
        }
    },
    mounted () {
        let id = this.$route.params.id;
        if(!id) {
            this.$route.push('/404');
            return false;
        }
        func.ajaxPost(
            api.getmodule,
            {
                id:id,
                state:0,
                type:1
            },
            res => {
                console.log(res.data.data);
                this.fileInfo.title = res.data.data.title;
                // this.fileInfo.filetype = res.data.data.filetype;
                this.selectedOptions = [res.data.data.filetype.split('-')[0],res.data.data.filetype.split('-')[1]];
                this.fileInfo.tags = res.data.data.tags;
                this.fileInfo.desc = res.data.data.desc;
                if(res.data.data.tags) {
                    this.taglist = JSON.parse(res.data.data.tags)
                }
            }
        )
    }
}
</script>

<style lang="less" scoped>
    aside{
        width: 375px;
        text-align: left;
        .person-oprate{
            height: 36px;
            font-size: 0;
            padding-left: 20px;
            padding-bottom: 10px;
            button{
                width: 72px;
                height: 34px;
                text-align: center;
                line-height: 36px;
                font-size: 15px;
                margin-right: 12px;
                border-radius: 5px;
                padding:0;
            }
            .praise,.collect{
                border: 1px solid #00b8d4;
                background-color: #fff;
                color: #00b8d4;
                &:hover{
                    background-color: #00b8d4;
                    color: #fff;
                }
            }
            .down,.share{
                border: 1px solid rgb(133,133,133);
                background-color: rgb(133,133,133);;
                color: #fff;
            }

        }
        .el-form {
            padding-left: 20px;
            padding-right: 20px;
        }
    }

</style>
<style lang="less">
    #draft {
        .el-input{
            .el-input__inner{
                width: 336px;
                height: 50px;
                text-align:left;
            }
        }
        .el-form-item{
            .el-form-item__label{
                line-height:20px;
            }
        }
        .tag{
            display: inline-block;
            line-height: 24px;
            padding: 5px 10px;
            font-size: 14px;
            background-color: #eee;
            border-radius: 5px;
            margin-right: 10px;        
        }
        .tagInput{
            width:200px;
            height: 30px;
            margin-top:10px;
            border:none;
            &:focus{
                border:none;
                outline:none;
            }
        }
        .getFocus{
            border-color:#409EFF!important;
        }
        .tag_box{
            -webkit-appearance: none;
            background-color: #fff;
            background-image: none;
            border-radius: 4px;
            border: 1px solid #dcdfe6;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            color: #606266;
            display: inline-block;
            font-size: inherit;
            line-height: 40px;
            outline: 0;
            padding: 0 15px;
            width:100%;
        }
    }
    
</style>






