<!-- 2D -->
<template>
  <div id="twoD" v-loading="loading">
    <div class="mainNav">
        <div class="subMainNavBox">
            <div class="mainNav-middle fl cur">
                <ul>
                    <li @click="recommendedTop" :class="[{defalutColor:active == 1}]"></li>
                    <li @click="trendingTop" :class="[{defalutColor:active == 2}]"></li>
                    <li @click="newTop" :class="[{defalutColor:active ==3}]"></li>
                </ul>
            </div>
            <div class="mainNav-right fr">
                <div class="">
                    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                        <el-submenu index="1">
                            <template slot="title"></template>
                            <el-submenu v-for="(option,index) in options" :key="index" :index="option.label">
                                <template slot="title">{{option.value}}</template>
                                <el-menu-item v-for="(suboption,subindex) in option.children" :key="subindex" :index="`${option.label}-${suboption.label}`">{{suboption.value}}</el-menu-item>
                                
                            </el-submenu>
                        </el-submenu>
                    </el-menu>
                </div>
                <div>
                    <!-- <el-checkbox v-model="checked"></el-checkbox>
                    <el-checkbox v-model="checked"></el-checkbox> -->
                </div>
            </div>
        </div>
    </div>
    <div class="container clear">
        <el-row type="flex" class="row-bg" justify="space-between" ref="list">
            <router-link :to="{path:'/model/detail/'+list.id}" v-for='(list,index) in lists' :key="index">
                <el-col class="img_single">
                    <div class="grid-content">
                        <img src="../../assets/img/twod/imglist1.png" alt="">
                    </div>
                    <div class="bachover" v-moduleHover></div>
                    <div class="moduleAuthor" v-moduleShow>
                        <router-link :to="{path:'/author/'+list.userid}">
                            <div class="author_avatar fl">
                                <img src="" alt="">
                            </div>
                            <div class="author_name fl">{{list.auth}}</div>
                        </router-link>
                    </div>
                </el-col>
            </router-link>
            <span style="display:inline;width:380px;height:0;border:none;"></span>
            <span style="display:inline;width:380px;height:0;border:none;"></span>
        </el-row>
        <div class="more" v-if="isAll" @click="loadMore"></div>
        <!-- <hr>
        <h1>{{setHeight}}</h1> -->
    </div>
  </div>
</template>

<script>
import func from '@/public/func';
import api from '@/public/api';
export default {
  name: 'Twod',
  data(){
      return {
          active:1,
          TwoDImg:[],
          activeIndex:'',
          checked:'',
          lists:[],
          isAll:false,
          loading:true,
          pagenum:1,
          pagesize:15,
          options:[
            {
            value:'',
            label:'',
            children:[{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },{
                value:'',
                label:''
            },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            },{
                value:'',
                label:'',
                children:[{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },]
            }
        ],

      }
  },
  mounted () {
    func.ajaxPost(
        api.modulelist, 
        {
            state:1,
            type:1,
            pagenum:this.pagenum,
            pagesize:this.pagesize
        },
        res => {
            this.loading = false;
            let resData = res.data;
            if(resData.status == 200){
                
                if(resData.data) {
                    this.lists = resData.data;
                    if(resData.data.length < this.pagesize) {
                        this.isAll = false;
                    }else{
                        this.pagenum += 1;
                    }
                }else if(resData.errmsg) {
                    this.Message(resData.errmsg,error)
                }
            }else{
                this.Message(resData.errmsg,error)
            }
    });
  },
  methods: {
      recommendedTop() {
          this.loading = true;
          this.pagenum = 1;
          this.active = 1;
          func.ajaxPost(
            api.modulelist, 
            {
                sortstate:1,
                type:1,
                pagenum:this.pagenum,
                pagesize:this.pagesize
            },
            res => {
                this.loading = false;
            let resData = res.data;
            if(resData.status == 200){
                if(resData.data) {
                    console.log(resData);
                    this.lists = resData.data;
                    if(resData.data.length < 15) {
                        this.isAll = true;
                    }else{
                        this.isAll = false;
                    }
                }else if(resData.errmsg) {
                    this.Message(resData.errmsg,error)
                }
            }else if(resData.status == 500){
                this.Message(resData.errmsg,error)
            }
        });
      },
      trendingTop() {
          this.loading = true;
          this.pagenum = 1;
          this.active = 2;          
          func.ajaxPost(
            api.modulelist, 
            {
                sortstate:2,
                type:1,
                pagenum:this.pagenum,
                pagesize:this.pagesize
            },
            res => {
                this.loading = false;
            let resData = res.data;
            if(resData.status == 200){
                if(resData.data) {
                    console.log(resData);
                    this.lists = resData.data;
                    if(resData.data.length < 15) {
                        this.isAll = false;
                    }else{
                        this.isAll = true;
                    }
                }else if(resData.errmsg) {
                    this.Message(resData.errmsg,error)
                }
            }else if(resData.status == 500){
                this.Message(resData.errmsg,error)
            }
        });
      },
      newTop() {
          this.loading = true;
          this.pagenum = 1;
          this.active = 3;          
          func.ajaxPost(
            api.modulelist, 
            {
                sortstate:3,
                type:1,
                pagenum:this.pagenum,
                pagesize:this.pagesize
            },
            res => {
                this.loading = false;
            let resData = res.data;
            if(resData.status == 200){
                if(resData.data) {
                    console.log(resData);
                    this.lists = resData.data;
                    if(resData.data.length < 15) {
                        this.isAll = false;
                    }else{
                        this.isAll = true;
                    }
                }else if(resData.errmsg) {
                    this.Message(resData.errmsg,error)
                }
            }else if(resData.status == 500){
                this.Message(resData.errmsg,error)
            }
        });
      },
      loadMore() {
          this.loading = true;
          func.ajaxPost(
            api.modulelist, 
            {
                state:1,
                type:1,
            },
            res => {
                this.loading = false;
                let resData = res.data;
                if(resData.status == 200){
                    if(resData.data) {
                        this.lists = [...this.lists,...resData.data];
                        if(resData.data.length < this.pagesize) {
                            this.isAll = false;
                        }else{
                            this.pagenum += 1;
                        }
                    }else if(resData.errmsg) {
                        this.Message(resData.errmsg,error)
                    }
                }else{
                    this.Message(resData.errmsg,error)
                }
            });
      },
      handleSelect(key, keyPath) {
          this.loading = true;
          this.lists = [];
        func.ajaxPost(
            api.modulelist, 
            {
                sortstate:5,
                filter:keyPath[1],
                type:1,
                pagenum:this.pagenum,
                pagesize:this.pagesize
            },
            res => {
                this.loading = false;
            let resData = res.data;
            if(resData.status == 200){
                if(resData.data) {
                    if(resData.data.msg) {
                        this.Message(resData.data.msg,'warning');
                        this.isAll = false;
                    }else{
                        this.lists = resData.data;
                        if(resData.data.length < 15) {
                            this.isAll = false;
                        }else{
                            this.isAll = true;
                        }
                    }
                    
                }else if(resData.errmsg) {
                    this.Message(resData.errmsg,'error')
                }
            }else{
                this.Message(resData.errmsg,'error')
            }
        });
      }

  }
}

</script>
<style lang="less" scoped>
    #twoD {
        width: 100%;
        margin: 0 auto;
        // height: 500px;
        margin-bottom: 2px;
        background-color: #f6f5f5;
        .mainNav{
            width: 100%;
            height: 58px;
            background: #fff;
            margin-top: 70px;
            .subMainNavBox{
                // padding: 0 140px;
                width: 1200px;
                margin: 0 auto;
                .mainNav-left{
                    width: 260px;
                    height: 36px;
                    margin-top: 11px;
                    margin-left: 20px;
                    // .el-input__inner{
                    //     border-radius: 20px;
                    //     background: rgb(217,217,217);
                    // }
                }
                .mainNav-middle{
                    font-size: 18px;
                    color: #333333;
                    margin-left: 414px;
                    ul{
                        li{
                            height: 58px;
                            line-height: 58px;
                            float: left;
                            margin-left: 10px;
                            margin-right: 60px;
                            &:active{
                                color: #5BC4EA;
                            }
                        }
                    }
                }
                .mainNav-right{
                    .el-submenu__title{
                        padding: 0;
                        padding-right: 20px;
                    }
                    .el-menu--collapse .el-menu .el-submenu, .el-menu--popup {
                        padding: 0;
                        margin: 0;
                        min-width: 100px!important;
                    }
                    // .el-menu--popup{
                    //     top: -5px;
                    //     margin: 0;
                    //     padding: 0;
                    //     min-width: 100px;
                    //     left: 16px;

                    // }
                    >div{
                        float: left;
                    }
                    ul{
                        border: none;
                    }
                    // li,>div,:hover{
                    //     height: 58px;
                    //     border: none;
                    // }
                    label{
                        height: 58px;
                        line-height: 58px;
                    }
                }
            }
        }
        .container{
            width: 1200px;
            margin: 0 auto;
            margin-top: 10px;
            @size:100%;
            box-sizing: border-box;
            .el-row--flex {
                flex-wrap:wrap;
                .img_single{
                    width: 380px;
                    height: 242px;
                    margin-top: 10px;
                    margin-bottom: 10px;
                    border-radius: 3px;
                    position: relative;
                    .grid-content{
                        width: 100%;
                        height: 100%;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .moduleAuthor{
                        bottom: 0;
                        padding: 10px;
                        height: 50px;
                        position: absolute;
                        box-sizing: border-box;
                        display: none;
                        .author_avatar{
                            width: 30px;
                            height: 30px;
                            margin-right: 10px;
                        }
                        .author_name{
                            color: #5BC4EA;
                            line-height: 30px;
                        }

                        }
                    .bachover{
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        &:hover{
                            background-image: linear-gradient(180deg,transparent,rgba(0,0,0,.3));
                            
                        }
                    }
                    
                }
            }
            .more{
                width: 100%;
                height: 32px;
                line-height: 32px;
                background:rgb(189,189,189);
                color: #fff;
                text-align: center;
                margin-bottom: 20px;
                margin-top: 10px;
                border-radius: 5px;
                clear: both;
            }
        }
    }
</style>
<style lang="less">
    #twoD .el-menu--horizontal>.el-submenu .el-submenu__title{
        padding-left: 0;
        padding-right: 105px;
        height: 58px;
        line-height: 58px;
    }
    #twoD .el-menu--horizontal>.el-submenu.is-active .el-submenu__title{
        border-bottom:0;
    }
    .el-submenu.is-active .el-submenu__title{
        border-bottom-color:#fff;
    }
    .el-menu--popup-bottom-start{
        margin-top: 0;
    }
    .el-menu--horizontal .el-menu--collapse .el-menu .el-submenu, .el-menu--popup{
        min-width: 100px!important;
    }
    .el-menu--popup-right-start{
        margin-left: 0;
    }
    .el-submenu .el-menu-item{
        min-width: 100px;
    }
    /* .el-checkbox__inner :hover{
        height: 13px;
        border: #409EFF;
    } */
    .show{
        display: block!important;  
        transition: display 10s; 
    }
    
    
</style>



